.toast {
  margin-top: 20rem !important;
  background: linear-gradient(90deg, #e921c3 0%, #0067ff 100%);
  border-radius: 0.5rem;
}

@media (min-width: 1024px) {
  .toast {
    margin-top: 7rem !important;
    background: linear-gradient(90deg, #e921c3 0%, #0067ff 100%);
    border-radius: 0.5rem;
  }
}

.coming {
  @apply mr-[0.5rem] h-[1.5rem] w-[1.5rem];
}
